<template>
  <div>
    <div class="container">
      <div class="py-5 ">
        <div class="row mb-5">
          <form action="" @submit.prevent="register">
            <div class="col-md-5 offset-md-6">
              <h3>注册</h3>
            </div>
            <div class="col-md-5 offset-md-4 form-floating  mb-3">
              <input v-model="username" type="text" class="form-control" id="inputaccount" name="account" placeholder="账号是唯一不可改变">
              <label for="inputaccount" class="form-label ">账号</label> 
              <p v-text="message" style="color: red;"></p>
            </div>
            <div class="col-md-5 offset-md-4 form-floating mb-3">
              <input v-model="password" type="password" class="form-control"  id="inputpassword" name="password" required="" placeholder="">
              <label for="inputpassword">密码</label>
            </div>
            <div class="col-md-5 offset-md-4 form-floating mb-3">
              <input v-model="password2" type="password" class="form-control"  id="inputpassword2" name="password" required="" placeholder="Password">
              <label for="inputpassword2">二次密码</label>
            </div>
            <div class="col-md-5 offset-md-4">
              <label for="gender" class="form-label">性别</label>
              <input v-model="gender" type="text" class="form-control" id="gender" placeholder="" >
            </div>
            <div class="col-md-5 offset-md-4">
              <label for="career" class="form-label">职业</label>
              <input v-model="career" type="text" class="form-control" id="career" placeholder="">
            </div>
            <div class="col-md-5 offset-md-4">
              <label for="email" class="form-label">email</label>
              <input type="text" class="form-control" id="email" placeholder="" required="">
            </div>
            <div class="offset-md-5 mt-3">
              <button type="submit"  class="btn btn-primary">提交</button>
              <button @click="$router.back()" class="btn btn-primary offset-md-2">返回</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    
        <hr>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: '',
      message: '',
      username: '',
      password: '',
      password2: '',
      gender: '',
      career: '',
      email: ''

    }
  },
  methods: {
    async postInfo() {
      // console.log(this.usern, this.passw)// 'test' '/bloguser/search/'
      const { data: res } = await this.$http.post('bloguser/', 
        this.$qs.stringify({ username: this.username, password: this.password, gender: this.gender, career: this.career, email: this.email}), )
        if (res.status === false){
          console.log(res.message);
          this.message = res.message
        }
        else if (res.status === true){
          console.log(res.message)
          this.data = res.data
        }
    },
    async register() {
      await this.postInfo()
      const userData = this.data
      this.$router.push({ name: 'mypage', params: {userData}})

    }
  }
}
</script>

<style lang="less" scoped>
#d2 {
  border: 1px solid black;
}
</style>