<template>
  <div>
    <div class="box">
      <div class="blog-container">
        <BlogBody 
          v-for="item in shuju"
          :key="item.id"
          :id="item.id"
          :title="item.title"
          :createtime="item.createtime"
          :summary="item.summary"
          :username="item.username"
          :labels="item.labels"
          :shuju="item"
        ></BlogBody>
      </div>
      <Label :cRecommend="categoryRecommend"></Label>
    </div>
  </div>
</template>

<script>
import BlogBody from '@/components/blogBody/BlogBody.vue'
import Label from '@/components/label/Label.vue'

export default {
  name: 'BlogLabel',
  components: {
    BlogBody,
    Label,
  },
  data() {
    return {
      shuju: [],
      categoryRecommend: ''
    }
  },
  methods: {
    async getInfo() {
      const {data: res} = await this.$http.get('/bloginfo/search/')
      this.shuju = res.data
    },
    async getlabel() {
      const {data: res} = await this.$http.get('/category/detail/')
      console.log(res.data)
      this.categoryRecommend = res.data
    }
  },
  created() {
    this.getInfo()
    this.getlabel()
  }
}
</script>

<style lang="less" scoped>
 .box {
    padding: 30px 200px 30px 200px;
    border: 0px rgb(55, 33, 29) solid;
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 100px;
  
    .blog-container {
      width: 100%;
      height: 100%; /* 设置高度为父容器高度 */
      // overflow: auto; /* 当内容超出容器高度时，显示滚动条 */
    }
  }
</style>