<template>

  <div class="text-center mt-5">
    <h1 class="mb-3">Login 界面</h1>
    <form @submit.prevent="login" @keyup.enter="login">
      <div class="col-md-5 offset-md-4 form-floating  mb-3">
        <input v-model="usern" type="text" class="form-control" id="inputaccount" name="account" placeholder="Password">
        <label for="inputaccount" class="form-label ">账号</label>
      </div>
      <div class="col-md-5 offset-md-4 form-floating mb-3">
        <input v-model="passw" type="password" class="form-control"  id="inputpassword" name="password" placeholder="Password" autocomplete="off">
        <label for="inputpassword">密码</label>
      </div>
      <div class="col-md-10 offset-md-1 mb-3">
        <button type="submit" class="btn btn-primary me-4" :disabled="!canSubmit"  @click="updateGlobalVariable(usern)" >登录</button>
        <!-- <router-link to="/register"><button class="btn btn-primary ms-5" @click="toRegister">注册</button></router-link> -->
        <button class="btn btn-primary ms-5" @click="toRegister">注册</button>
        <span>{{ this.usern }}</span>
        <span>{{ this.passw }}</span>
      </div>

    </form>
    <div>
        <button class="btn btn-outline-primary" @click="open1">提示</button>
      </div>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
import request from '@/utils/request.js'
// import Register from '@/views/Register.vue'
// import eventBus from '../components/eventBus'
// axios({
//   url: '',
//   method: 'post',
//   data: {
//     username: '',
//     password: ''
//   }
// }).then(result => {
//   console.log(result)
// }).catch(error => {
//   // 处理错误信息
//   console.log(error)
// })

export default {

  data() {
    return {
      shuju: [],
      usern: '',
      passw: '',
      str: '这是Login的数据',
      token: ''
    }
  },
  computed: {
    canSubmit() {
      return Boolean(this.usern && this.passw)
    },
    globalVariable() {
      return this.$store.state.globalUsername
    }
  },
  methods: {
    updateGlobalVariable(newValue) {
        this.$store.dispatch('updateGlobalVariable', newValue)
      },
    open1() {
      this.$message('这是一条消息提示')
      console.log(this.$message)
    },
    // async getIfo() {
    //   const {data: res} = await axios.get('http://127.0.0.1:8000/category/search/all/')
    //   this.shuju = res
    // },
    async postInfo() {
      // console.log(this.usern, this.passw)// 'test' '/bloguser/search/'
      const { data: res } = await request.post('/bloguser/search/', 
        this.$qs.stringify({ username: this.usern, password: this.passw}), 
        )
        console.log("这是res", res)
      if (res.status === true){
        this.shuju=res.data
        this.token = res.token
        // 登录成功
        // 1.存储token
        console.log(this.token)
        localStorage.setItem('token', `Bearer ${this.token}`)
        localStorage.setItem('username', this.usern)
      } else {
        // 登录失败
        localStorage.removeItem('token')
        alert("账号或者密码错误")
      }
    },
    async login(){
      await this.postInfo()
      // this.$router.push('/mypage')
      const userData = this.shuju
      console.log('这是用户名', this.usern)
      this.$router.push({ name: 'mypage', params: {userData}})
      console.log("这是userData", userData)

    },
    toRegister() {
      this.$router.push('/register')
    }
    
  },
  created() {
    // this.postInfo()
    // this.shuju = '访问成gong'
    
  },
  
}
</script>

