        { "classification": 15, "title": "操作系统", "createtime": "2024-01-19T20:08:21Z" } ],
      // 已选择的标签
      selectedTags: [], 
    };
  },
  computed: {
    remainingTagSlotsMessage() {
      const remainingSlots = 9 - this.selectedTags.length;
      return `还可以添加${remainingSlots}个标签`;
    }
  },
  methods: {
    // 点击错号按钮会删除数组中对应的索引
    removeTag(index) {
      this.selectedTags.splice(index, 1);
    },
    // 从标签栏中点击span添加到input
    addTag(t) {
      if(this.selectedTags.length < 10){
        this.selectedTags.push(t)
      }else{
         // 禁止添加数据
        console.log("数组长度已达到上限，禁止添加新数据！");
      }
      
    },
    // 输入字符按下回车添加到select数据中
    add(e) {
      // 如果input值大于10，禁止输入
      if (this.selectedTags.length >= 10) {
        e.preventDefault(); // 阻止默认事件（输入）
      }else{
        this.selectedTags.push(e.target.value)
      this.clearInput()
      }

    },
    // 再按下回车后将已添加的数据，在input框中清楚
    clearInput() {
      document.getElementById('i1').value = ''; 
    },
    async getIfo() {
      const {data: res} = await this.$http.get('/bloginfo/search/all/')
      this.shuju = res.data
      // 将后端的数据化进行解json
      var jsonData = JSON.parse(this.shuju);
      var fieldsValue = jsonData[0].fields;
      this.shuju = fieldsValue
      
      console.log(res)
      console.log(this.shuju)
    },
  },
  created() {
    this.getIfo()
  }

}
</script>

