<template>
  <div class="container">
    <h1 class="text-center">可视化界面</h1>
    <div class="d-flex align-items-center">
      <!-- <p>{{this.blog_data}}</p> -->
      <e-charts
        class="chart"
        :options="option"
      />
      <div class="mr-auto">
        <div >
          <p class="text-center">本网站共有{{ sData.blog_count }}个博客</p>
        </div>
        <div class="text-center">
          <p>本网站共有{{ sData.category_count_all }}个标签</p>
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-end">
      <e-charts class=""
      :options="option2"
    />
    </div>
    
    <!-- <v-chart class="chart"
      :options="option" /> -->
  </div>
</template>

<script>
// import VChart, { THEME_KEY } from "vue-echarts";
import request from '@/utils/request.js'

export default {
  // components: {
  //   VChart
  // },
  // provide: {
  //   [THEME_KEY]: "dark"
  // },
  data() {
    return {
      sData: {},
      blog_data: [],
      categoryData: [],
      // data: [
      //   {
      //     time: '2018-01-01',
      //     value: Math.random() * 100,
      //   },
      //   {
      //     time: '2018-01-01',
      //     value: Math.random() * 100,
      //   },
      //   {
      //     time: '2018-01-01',
      //     value: Math.random() * 100,
      //   },
      //   {
      //     time: '2018-01-01',
      //     value: Math.random() * 100,
      //   },
      // ],
    } 
  },
  computed: {
    option() {
      return {
        // 图表的标题
        title: {
          text: '博客点赞数最多的五个'
        },
        // 提示框的配置,trigger 属性指定了何时触发提示框显示 这里设置为, 'axis'，表示当鼠标悬停在坐标轴上时显示。
        tooltip: {
          trigger: 'axis'
        },
        // 图例（legend）的配置
        legend: {
          data: ['likes', 'dislikes', 'comments', 'collect']
        },
        // 图表区域内的网格布局。它指定了网格的位置和大小
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        // 定义了工具箱（toolbox）的配置,提供了与图表交互的选项，比如保存为图片等。
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        // x轴（横轴）的配置
        xAxis: {
          type: 'category',
          boundaryGap: false,
          // data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
          data: this.blog_data.map(d => d.blog_id),
        },
        // y轴（纵轴）的配置
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: 'likes',
            type: 'line',
            stack: 'Total',
            // data: [120, 132, 101, 134, 90, 230, 210]
            data: this.blog_data.map(d => d.likes)
          },
          {
            name: 'dislikes',
            type: 'line',
            stack: 'Total',
            // data: [220, 182, 191, 234, 290, 330, 310]
            data: this.blog_data.map(d => d.dislikes)
          },
          {
            name: 'comments',
            type: 'line',
            stack: 'Total',
            // data: [150, 232, 201, 154, 190, 330, 410]
            data: this.blog_data.map(d => d.comments)
          },
          {
            name: 'collect',
            type: 'line',
            stack: 'Total',
            // data: [320, 332, 301, 334, 390, 330, 320]
            data: this.blog_data.map(d => d.collect)
          },
          // {
          //   name: 'blog',
          //   type: 'line',
          //   stack: 'Total',
          //   // data: [820, 932, 901, 934, 1290, 1330, 1320]
          //   data: this.blog_data.map(d => d.blog)
          // }
        ]
      }
    },
    option2() {
      return {
        xAxis: {
        type: 'category',
        // data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        data: this.categoryData.map(d => d.title)
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            // data: [120, 200, 150, 80, 70, 110, 130],
            data: this.categoryData.map(d => d.count),
            type: 'bar',
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(180, 180, 180, 0.2)'
            }
          }
        ]
      }
    },
  },
  methods: {
    async get_data() {
      const {data: res} = await request.get('echarts/')
      if (res.status === true) this.sData = res.data.data[0], this.blog_data=res.data.blog_data, this.categoryData= res.data.category_count
      console.log('这是1111', res.data)
      console.log('这是123456', this.sData)
      console.log('这是123456', this.blog_data)
    }
  },
  created() {
    this.get_data()
  },
  mounted() {
  }
};
</script>

<style lang="less" scoped>
  .chart {
    height: 400px;
    width: 800px;
  }

</style>