<template>
  <div>
    <!-- header -->
    <!-- <div class="top" >
        <div class="topmiddle" id="mytop1">
            <div class="container" id="topcenter">
                <div class="row">
                    <div class="col-md-7 offset-md-1"><router-link to="/">首页</router-link></div>
                    <div class="col-md-1 "><router-link to="/visualization">可视化</router-link></div>
                    <div class="col-md-1 "><router-link to="/mypage">博客管理</router-link></div>
                    <div class="col-md-1 "><router-link to="/login">退出</router-link></div>
                </div>
            </div>
        </div>
        <hr>
    </div> -->
    <!-- 修改博客 -->
    <div class="container" id="write">
      <div class="row" id="d1">
        <h1 class="">修改博客</h1>
        <form @submit.prevent="login">
          <div class="form-group">
            <label for="exampleInputText1">标题</label>
            <input type="text" class="form-control" id="exampleInputText1" v-model="title">
          </div>
          <div class="form-group">
            <label for="exampleFormControlTextarea1" >简介</label>
            <textarea class="form-control" id="exampleFormControlTextarea1" rows="2" v-model="summary"></textarea>
          </div>
          <div class="form-group">
            <label for="markdown-editor">正文</label>
            <!-- <textarea class="form-control" id="exampleFormControlTextarea2" rows="3" v-model="text"></textarea> -->
            <textarea class="form-control" id="markdown-editor " rows="3" v-model="text"></textarea>
            <mavon-editor v-model="text" :toolbars="toolbars" />
          </div>
          <!-- // 标签选择 -->
          <div class="form-group mt-2">
            <div class=" mb-3">
              <div class="d-flex align-items-center ">
                <div class="p-2"> 标签:</div>
                <div class="kuang p-2 d-flex align-items-center border border-dark rounded mx-3 my-3" id="div1">
                  <div v-if="selectedTags.length>0" id="cd1" title="点击标签会加入到这里">
                    <div class="custom-tag bg-primary text-white mx-1 my-1" id="sp1" v-for="(tag, index) in selectedTags" :key="index">
                      <span >{{tag}}</span>
                      <button class="close-btn text-white" id="cb1" @click="removeTag(index)">×</button>
                    </div>
                  </div>
                  <div v-else id="cd1" title="点击标签会加入到这里"></div>
                  <div class="flex-grow-1">
                    <input class="form-control border-0 " id="i1" list="tagsList" type="text" placeholder="按回车键Enter创建标签" @keydown.enter.prevent="add"> 
                  </div>
                  <div class="float-right">
                    <p class="font-weight-normal m-0 ml-2">还可以添加{{ 10-selectedTags.length }}个标签</p>
                  </div>
                  
                </div>
              </div>
              <div class="biaoqianlan">
                <span class="btn btn-outline-primary border border-primary my-2 mx-2 " v-for="tag in tags" :key="tag.classification" @click="addTag(tag.title)">{{ tag.title }}</span>
                <datalist id="tagsList">
                  <option v-for="tag in tags" :key="tag.classification" :value="tag.title">{{ tag.title }}</option>
                </datalist>
              </div>
            </div>
          </div>
          <!-- // 按钮 -->
          <div class="offset-md-3 mb-2">
            <button type="submit" class="btn btn-primary me-4" >提交</button>
            <button @click="$router.back()" class="btn btn-primary offset-md-2">返回</button>
          </div>
          
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blog: [],
      inputText: '',
      // 从后端请求到的数据 放在tags列表当中
      tags: {}, // 替换成你的标签
      selectedTags: [],
      // form 表单提交的数据
      title: '',
      summary: '',
      text: '',
      // 账户名从mypage页面传数据，或者账户名是公有的数据 
      username: '',
      // classification: ''
      context: ' ',//输入的数据
      toolbars: {
          bold: true, // 粗体
          italic: true, // 斜体
          header: true, // 标题
          underline: true, // 下划线
          mark: true, // 标记
          superscript: true, // 上角标
          quote: true, // 引用
          ol: true, // 有序列表
          link: true, // 链接
          imagelink: true, // 图片链接
          help: true, // 帮助
          code: true, // code
          subfield: true, // 是否需要分栏
          fullscreen: true, // 全屏编辑
          readmodel: true, // 沉浸式阅读
          undo: true, // 上一步
          trash: true, // 清空
          save: true, // 保存（触发events中的save事件）
          navigation: true // 导航目录
      }
    }
  },
  methods: {
    // 获取部分标签
    async getCategory() {
      const { data: res } = await this.$http.get('/category/detail/')
      this.tags = res.data
      // 将数据重新放在一个数组[]中
      // this.tags = this.tags.map(item => item.title)
      console.log('123', this.tags)
      },
    // 发送修改请求
    async postInfo() {
      const data = {blog_id: this.blog.id, title: this.title,
          summary: this.summary,
          text: this.text, username: this.username,
          classification: JSON.stringify(this.selectedTags)}
      await this.$http.post('/bloginfo/update/', 
        this.$qs.stringify(data)
      )},
    async login() {
      await this.postInfo()
      this.title = '',
      this.summary = '',
      this.text = '',
      this.selectedTags = [],
      this.$router.go(-1);
    },
    // 点击错号按钮会删除数组中对应的索引
    removeTag(index) {
      this.selectedTags.splice(index, 1);
    },
    // 从标签栏中点击span添加到input
    addTag(t) {
      if(this.selectedTags.length < 10){
        this.selectedTags.push(t)
        console.log(this.selectedTags)
      }else{
         // 禁止添加数据
        console.log("数组长度已达到上限，禁止添加新数据！");
      }
    },
    // 输入字符按下回车添加到select数据中
    add(e) {
      // 如果input值大于10，禁止输入
      if (this.selectedTags.length < 10) {
        console.log(e.target.value)
        this.selectedTags.push(e.target.value)
        console.log(this.selectedTags)
        this.clearInput()  
      }else{
        e.preventDefault(); // 阻止默认事件（输入）
      }

    },
    // 再按下回车后将已添加的数据，在input框中清楚
    clearInput() {
      document.getElementById('i1').value = ''; 
    }
  },
  created() {
    this.username = localStorage.getItem('username')
    this.blog = JSON.parse(this.$route.query.blog);
    this.getCategory()
    // 标签赋值
    this.selectedTags = this.blog.classification
    console.log(this.tags)
    // 标题赋值
    this.title = this.blog.title
    this.summary = this.blog.summary
    this.text = this.blog.text
  }
}
</script>

<style lang="less" scoped>
#write{
        height: 100%;
        /*margin: 0;*/
        display: grid;
        place-items: center; /* 水平和垂直居中 */
        #d1 {
          height: 100%;
          width: 80%;
          border: 1px solid black;
          background-color:antiquewhite
        }
        form {
          div {
            span {
              margin: 2px, 2px, 2px, 2px;
            }
          }
        }
        #div1{
        width: 90%;

        #i1:focus {
          outline: none; /* 去掉聚焦时的外边框 */
          border: none; /* 去掉聚焦时的边框 */
        }
        #sp1 {
          display: inline-flex;
          align-items: center;
          border: 0px solid #ccc;
          padding: 0.3rem 0.5rem;
          border-radius: 4px;
          margin-right: 0.5rem;
          
        }

        #sp1 span {
          margin-right: 0.5rem;
        }

        #cb1 {
          background-color: transparent;
          border: none;
          cursor: pointer;
          font-size: 12px;
          font-weight: bold;
          color: #666;
        }

        .close-btn:hover {
          color: #f00; /* 鼠标悬停时改变颜色 */
        }
      }
    }
</style>