import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '@/views/Login.vue'
import Mypage from '@/views/Mypage.vue'
import Visualization from '@/views/Visualization.vue'
import Write from '@/views/Write.vue'
import Index from '@/views/Index.vue'
import Register from '@/views/Register.vue'
import Test from '@/views/Test.vue'
import Bloginfo from '@/views/Blogshow.vue'
import Manageblog from '@/views/Manageblog.vue'
import Adminmanage from '@/views/Adminmanage.vue'
import Comment from '@/components/Comment.vue'
import BlogUpdate from '@/views/BlogUpdate.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/index' },
  // 定义首页路由规则
  // { path: '/', name: 'index', component: Index },
  { path: '/index', name: 'index', component: Index },
  { path: '/login',  name: 'login', component: Login },
  { 
    path: '/mypage',  
    name: 'mypage', 
    component: Mypage,
    children: [

    ]
  },
  // 博客详情页的路由规则
  { path: '/mypage/bloginfo/:id', name: 'mypage-bloginfo',  component: Bloginfo, props: true },
  // 写博客
  { path: '/mypage/write', component: Write},
  { path: '/visualization', component: Visualization },
  
  { path: '/register', component: Register},
  { path: '/test', component: Test},
  // 管理博客
  { path: '/manageblog', component: Manageblog},
  // 管理的博客的详情页
  { path: '/manageblog/update/:id' , name: 'blog-update' , component: BlogUpdate, props: true},

  { 
    path: '/adminmanage', 
    component: Adminmanage,
    meta: { authRequired: true }
  },
  { path: '/comment', component: Comment}


]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  if (to.name != 'index' && to.path != '/login' && to.path != '/register' && to.path != '/visualization'){
    if (token) {
      next()
    }else {
      next('/login') // 没token 跳转到登录页面
    }
  }else{
    next()
  }
})

