<template>
  <div>
    <div class="container">
      <div class="row">
        <!-- 展示评论模块 -->
        <!-- <div class="offset-md-3 col-md-6"> -->
        <div class="">
            <ul class="list-unstyled">
                <li class="d-flex align-items-start" >
                    <img src="@/assets/images/morentouxiang.png" class="mr-3" alt="...">
                    <div class="media-body">
                      <!-- 用户名 时间 -->{{ commentInfo.id }}
                        <h6 class="mt-0 mb-1 mt-2">{{ commentInfo.user }} -{{ commentInfo.created_at | formatDate }}</h6>
                        <!-- 内容 -->
                        <p class="mt-2">{{ commentInfo.text }}</p>
                        <p class="text-end">
                          <button v-show="true" class="btn btn-outline-info" type="button" data-bs-toggle="collapse" :data-bs-target="'#second'+this.commentInfo.id" aria-expanded="false" :aria-controls="'second'+this.commentInfo.id" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: .75rem;">
                            评论
                          </button>
                          <button v-show="commentInfo.child_comments.length !== 0" class="btn btn-outline-info" type="button" data-bs-toggle="collapse" :data-bs-target="'#second'+commentInfo.id" aria-expanded="false" :aria-controls="'second'+commentInfo.id" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: .75rem;">
                          展开
                        </button>
                        </p>
                         <!-- 用户提交对评论的评论 -->
                        <div class="collapse" :id="'second'+this.commentInfo.id">
                            <div class="input-group mb-3 ">
                              <div class="input-group-prepend">
                                <span class="input-group-text" id="basic-addon1">评论</span>
                              </div>
                              <textarea class="form-control" aria-label="With textarea" v-model="text"></textarea>
                              <button class="btn btn-primary" type="button" @click="postComment(commentInfo.id)">发表</button>
                            </div>
                          </div>
                          <!-- 展示父评论的子评论 -->
                        <div class = "collapse" :id="'second'+commentInfo.id" v-for="child_obj in commentInfo.child_comments" :key="child_obj.id" >
                          <ul class="list-unstyled ">
                            <li class="media d-flex align-items-start my-3">
                                <img src="@/assets/images/morentouxiang.png" class="mr-3" alt="...">
                                <div class="media-body">
                                  <!-- 用户名 时间 -->
                                    <h6 class="mt-0 mb-1 mt-2">{{ child_obj.user }} -- {{ child_obj.created_at | formatDate  }}</h6>
                                    <!-- 内容 -->
                                    <p class="mt-2">{{ child_obj.text }}</p>
                                </div>
                            </li>
                        </ul>
                        </div> 
                    </div>
                </li>
            </ul>
        </div>
      </div>
    </div>
      
  </div>
</template>

<script>
import bootstrap from 'bootstrap/dist/js/bootstrap.js'
// 导入requestapi
import request from '@/utils/request.js'
// import SecondComment from '@/components/SecondComment.vue'

export default {
  props: ['blog_id','commentInfo'],
  components: {
    // SecondComment
  },
  data() {
    return {
      text: '',
      username: '',
      isExpanded: false, // 控制是否展开所有评论内容,
      // commentInfo: ''
    }
  },
  methods: {
    toggleCollapse() {
      var collapseElement = document.getElementById('second');
      var bsCollapse = new bootstrap.Collapse(collapseElement);
      bsCollapse.toggle();
    },
    // 添加子评论
    async postComment(id) {
      const {data: res} = await request.post('comment/',
        { blog_id: this.blog_id, text: this.text, username: this.username, parent_comment: id},
        )
      if (res.status === true) alert("评论添加成功"), this.text=''
    }
  },
  created() {
    },
  mounted() {
    console.log('打印博客id1111', this.blog_id)
    this.username = localStorage.getItem('username');
    console.log('这是登录用户的username', this.username)
    console.log("这是评论的子评论是否有值", this.commentInfo)
    }
}
</script>

