import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import store from './store'
// 引入 ECharts
import 'echarts';
import ECharts from '../node_modules/vue-echarts/dist/vue-echarts'

// 引入qs
import qs from 'qs';

// Bootstrap
import 'bootstrap/dist/css/bootstrap.min.css'

// markdown
import mavonEditor from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
Vue.use(mavonEditor);

// ElementUI
// import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
// Vue.use(ElementUI);


// 全局注册Echarts组件
Vue.component('ECharts', ECharts);

Vue.config.productionTip = false

Vue.prototype.$qs = qs;

// 全局配置 axios 的请求根路径
axios.defaults.baseURL = 'http://127.0.0.1:8000/app01/'
// 解决点击两次登录问题
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
// 把 axios 挂载到 Vue.prototype 上，供每个 .vue 组件的实例直接使用
Vue.prototype.$http = axios

// 今后，在每个 .vue 组件中要发起请求，直接调用 this.$http.xx

// 定义全局过滤器
Vue.filter('formatDate', function(value) {
  if (!value) return '';
  const date = new Date(value);
  return date.toLocaleString(); // 你可以根据需要选择不同的日期格式化方法
});

new Vue({
  store,  // 注入 Vuex store
  router,
  render: h => h(App)
}).$mount('#app')
