       const {data: res} = await request.post('bloginfo/search/all/',
        // this.$qs.stringify({ username: this.usern, password: this.passw}), 
        this.$qs.stringify({ id: this.blogid }),
        )
      // 将 JSON 数据解析为 JavaScript 对象
      var parsedData = JSON.parse(res.data)
      // 获取 "fields" 字段的值
      var fieldsValue = parsedData[0].fields;
      this.blogdata = fieldsValue
      this.markdownText = fieldsValue.text
      // console.log(res.data,fieldsValue.text)
