        this.$qs.stringify({"id": this.id}),
      )
      // 将点赞等数量进行获取
      // 将 JSON 数据解析为 JavaScript 对象
      var parsedData = JSON.parse(res.attached_data)
      // 获取 "fields" 字段的值
      var fieldsValue = parsedData[0].fields;
      // 这是标签数据
      this.attached = fieldsValue
      // 将标签数据进行获取
      // console.log(res.classification_data)
      this.label = res.classification_data
      // var parsedData2 = JSON.parse(res.classification_data)
      // console.log(11,this.label)
    },
     // 点赞按钮点击事件
