    //     const index = likedBlogs.indexOf(this.id);
    //     if (index !== -1) {
    //         likedBlogs.splice(index, 1);
    //         localStorage.setItem('likedBlogs', JSON.stringify(likedBlogs));
    //     }
    //   }
    //   else if (this.likeStatus == false) {
    //     const { data: res } = await request.post('attached/update/', { id: this.id, like: 1 })
    //     if (res.status === true) {
