    //       this.attached.like += 1
    //       this.likeStatus = true
    //        // 将当前博客的 ID 添加到本地存储中，表示用户已点赞该博客
    //       likedBlogs.push(this.id);
    //       localStorage.setItem('likedBlogs', JSON.stringify(likedBlogs));
    //     }
    //     }
