    //             localStorage.setItem('dislikedBlogs', JSON.stringify(dislikedBlogs));
    //         }
    //     }
    //   }
    // },
    // 查询此用户在该博客的点赞，不喜欢收藏记录。
    async postBlogLike() {
      const { data:res } = await request.post('search/bloglike/', 
        {blog_id: this.id,username: this.username })
      console.log('// 查询此用户在该博客的点赞，不喜欢收藏记录。', res)
      if (res.status === true){
        console.log('// 查询此用户', res.data[0])
        // 喜欢按钮的状态
        this.likeStatus =  res.data[0].like,
        // // 不喜欢按钮的状态
        this.dislikeStatus = res.data[0].dislike,
        // // 收藏按钮的状态
        this.collectStatus = res.data[0].collect
      }
      
    },
    // 用户点击喜欢按钮
    async likeButton() {
      if (this.likeStatus === false && this.dislikeStatus === false) {
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, like: 1})
          if (res.status === true) this.likeStatus = true, this.attached.like = this.attached.like+1
      }else if (this.likeStatus === false && this.dislikeStatus === true) {
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, like: 1, dislike: 0})
        if (res.status === true) this.likeStatus = true, this.dislikeStatus = false, this.attached.like = this.attached.like+1
      }else if (this.likeStatus === true && this.dislikeStatus === false ){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, like: 0})
        if (res.status === true) this.likeStatus = false, this.attached.like = this.attached.like-1
      }
      
    },
    // 用户点击不喜欢按钮
    async disLikeButton(){
      if(this.dislikeStatus === false && this.likeStatus === false){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, dislike: 1})
        if (res.status === true) this.dislikeStatus = true
      }else if(this.dislikeStatus === false && this.likeStatus === true){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, like: 0, dislike: 1})
        if (res.status === true) this.dislikeStatus = true, this.likeStatus= false,this.attached.like = this.attached.like-1
      }else if(this.dislikeStatus === true && this.likeStatus === false){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, like: 0, dislike: 0})
        if (res.status === true) this.dislikeStatus = false
      }
    },
    // 用户点击收藏按钮
    async collectButton(){
      if(this.collectStatus === false){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, collect: 1})
        if (res.status === true) this.collectStatus = true
      }else if(this.collectStatus === true){
        const { data:res } = await request.post('bloglike/',
          {blog_id: this.id, username: this.username, collect: 0})
          if (res.status === true) this.collectStatus = false
      }
    },
    // 添加评论
    async postComment() {
      const {data: res} = await request.post('comment/',
        { blog_id: this.blog_id, text: this.text, username: this.username},
        )
      if (res.status === true) alert("评论添加成功")
    },
    // 查询该博客的一些评论
    async postCommentText() {
      const {data: res} = await request.post('comment/search/',
        { blog_id: this.id, page: this.page++},
        )
      if (res.status === true){
        console.log("这是博客id11", res.data)
        // 使用 concat 方法将新获取的评论数据与已有的评论数据进行合并
        // this.commentInfo = this.commentInfo.concat(res.data); 
        this.commentInfo = [...this.commentInfo, ...res.data]

      }
    },
    
  },
  async created() {
    this.blogid = this.id
    console.log("这是博客id", this.id);
    this.postInfo();
    this.postLike();
    // 初始化点赞的博客列表
    // this.likedBlogs = JSON.parse(localStorage.getItem('likedBlogs')) || [];
    // console.log(this.likedBlogs)
    // 初始化不喜欢的博客列表
    // this.dislikedBlogs = JSON.parse(localStorage.getItem('dislikedBlogs')) || [];
    // console.log(this.dislikedBlogs)
    this.username = this.$store.state.globalUsername
    this.username = localStorage.getItem('username')
    // console.log('这是全局名称',this.$store.state.globalUsername)
    console.log('这是全局名称',this.username)
    this.postBlogLike()
    this.postCommentText()
  },
  mounted() {
    // this.blogid = this.id;
    // this.postInfo();
    // this.postLike();
    // this.username = this.$store.state.globalUsername;
