<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col-12">
          <div class="card mt-5">
            <div class="card-header ">
              <h3 class="card-title">博客管理</h3>
            </div>
            <div class="card-body border-bottom py-3">
              <div class="d-flex">
                <div class="text-muted">
                  Show
                  <div class="mx-2 d-inline-block">
                    <input type="text" class="form-control form-control-sm" value="8" size="3" aria-label="Invoices count">
                  </div>
                  entries
                </div>
                <div class="ms-auto text-muted">
                  Search:
                  <div class="ms-2 d-inline-block">
                    <input type="text" class="form-control form-control-sm" aria-label="Search invoice">
                  </div>
                </div>
              </div>
            </div>
            <div class="table-responsive">
              <table class="table card-table table-vcenter text-nowrap datatable">
                <thead>
                  <tr>
                    <th class="w-1"><input class="form-check-input m-0 align-middle" type="checkbox" aria-label="Select all invoices"></th>
                    <th class="w-1">id <!-- Download SVG icon from http://tabler-icons.io/i/chevron-up -->
                      <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-sm text-dark icon-thick" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><polyline points="6 15 12 9 18 15"></polyline></svg>
                    </th>
                    <th>标题</th>
                    <th>简介</th>
                    <th>标签</th>
                    <th>创建时间</th>
                    <th>状态</th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="blog in blogs" :key="blog.id">
                    <td><input class="form-check-input m-0 align-middle" type="checkbox" aria-label="Select invoice"></td>
                    <!-- id -->
                    <td><span class="text-muted">{{ blog.id }}</span></td>
                    <!-- 标题 -->
                    <td><a href="" class="text-reset" tabindex="-1" @click.prevent="upadateBlog(blog)">{{ blog.title.slice(0, 30) }}</a></td>
                    <!-- 简介 -->
                    <td class="summary-cell">
                      
                      {{ blog.summary.slice(0, 10) }}
                    </td>
                    <!-- 标签 -->
                    <td>
                      {{ blog.classification }}
                    </td>
                    <!-- 创建日期 -->
                    <td>
                      {{ blog.createtime | formatDate }}
                    </td>
                    <!-- 状态 -->
                    <td>
                      <span class="badge bg-success me-1"></span> {{ blog.status === '1' ? '通过' : blog.status === '2' ? '通过' : '未通过' }}
                    </td>
                    <!-- 修改 -->
                    <td>
                      <button class="btn btn-success" @click="upadateBlog(blog)">修改</button>
                    </td>
                    <!-- 删除 -->
                    <td >
                      <button class="btn btn-danger" @click="deleteBlog(blog.id)">删除</button>
                      <!-- <span class="dropdown">
                        <button class="btn dropdown-toggle align-text-top" data-bs-boundary="viewport" data-bs-toggle="dropdown" aria-expanded="false">Actions</button>
                        <div class="dropdown-menu dropdown-menu-end" style="">
                          <a class="dropdown-item" href="#">
                            Action
                          </a>
                          <a class="dropdown-item" href="#">
                            Another action
                          </a>
                        </div>
                      </span> -->
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <!-- // 下一页模块 -->
            <div class="card-footer d-flex align-items-center">
              <p class="m-0 text-muted">Showing <span>1</span> to <span>8</span> of <span>16</span> entries</p>
              <ul class="pagination m-0 ms-auto" >
                <li class="page-item disabled">
                  <a class="page-link" href="#" tabindex="-1" aria-disabled="true">
                    <!-- Download SVG icon from http://tabler-icons.io/i/chevron-left -->
                    <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><polyline points="15 6 9 12 15 18"></polyline></svg>
                    prev
                  </a>
                </li>
                <li class="page-item"><a class="page-link" href="#">1</a></li>
                <li class="page-item active"><a class="page-link" href="#">2</a></li>
                <li class="page-item"><a class="page-link" href="#">3</a></li>
                <li class="page-item"><a class="page-link" href="#">4</a></li>
                <li class="page-item"><a class="page-link" href="#">5</a></li>
                <li class="page-item">
                  <a class="page-link" href="#">
                    next <!-- Download SVG icon from http://tabler-icons.io/i/chevron-right -->
                    <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><polyline points="9 6 15 12 9 18"></polyline></svg>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import request from '@/utils/request.js'

export default {
  props: [],
  data() {
    return {
      username: '',
      page: 1,
      blogs: []
    }
  },
  methods: {
    async postBlog() {
      const {data: res} = await request.post('blogs/',
        { username: this.username, page: this.page++},
        )
      if (res.status === true) console.log(res), this.blogs = res.data
    },
    upadateBlog(blog) {
      this.$router.push({
        path: '/manageblog/update/' + blog.id,
        query: { blog: JSON.stringify(blog) }
      })
    },
    async deleteBlog(id){
      const {data: res} = await request.post('bloginfo/delete/',
        { blog_id: id},
        )
      if (res.status === true) alert(res.message)
    }
  },
  created() {
    this.username = localStorage.getItem('username')
    console.log(this.username)
    this.postBlog()
  }

}
</script>

<style lang="less" scoped>
  .summary-cell {
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>